  position relative
  top 0.8rem
  height calc(100% - 0.8rem)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .inner
      width 100%
      height calc(100% - 0.28rem)
    .c-left
      position relative
      flex 1

      .map-back
        position absolute
        top 30px
        right 60px
        width 30px
        height 30px
        background url("~@/assets/back-icon.png") no-repeat
        z-index 1000
        font-size 0.2rem
        color #fff
        cursor pointer
      .map
        position absolute
        top -40px
        left 100px
      .detail
        position absolute
        top 0.6rem
        left 0.6rem
        z-index 1000

        .allNumber
          font-size 0.2rem
          color #5aceff
          margin-bottom 0.2rem
          .number
            font-size 0.5rem
            color #fff
            text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
            margin-right 0.15rem
        .top3
          .label
            float left
            width 0.5rem
            color #ffa800
            font-size 0.18rem
            font-weight 700
            line-height 0.4rem

          .list
            float left
            .item
              line-height 0.4rem
              font-size 0
              color #fff
              .order
                font-size 0.18rem
                margin-right 0.1rem
              .name
                display inline-block
                width 0.8rem
                font-size 0.18rem
              .number
                font-size 0.18rem
              .unit
                font-size 0.14rem
                color #5aceff
                margin-left 0.1rem
    .c-right
      flex 1
      display flex
      flex-direction column
      .timeChange
        top 0
        right .8rem
      .c-r-t
        flex 1
        position relative
      .c-r-c
        flex 1

    .c-r-b
      flex 1
      height: calc(100% - 0.28rem);
      display flex
      flex-direction row
      padding 0 10px 0 20px
        .inner2
          flex 1
          .label2
            font-size 12px
</style>
